@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,600;0,700;1,500&display=swap'); // * {
$colorV:#00BFA6;
$colorOr:#F9A826;
$colorR:#F50057;
$colorW:#FFFFFF;

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}

.Navbar {
    display: flex;
    justify-content: space-between;
    background-color: $colorV;
    padding: 1.5em 2.5em;

    .logo {
        font-size: 2vw;
    }

    .inscr {
        font-size: 1vw;
        text-decoration: none;
        font-weight: 700;
    }
}

.header {
    display: flex;
    justify-content: space-between;
    background-color: $colorV;
    padding: 1em 2em;

    &__text {

        h1 {
            margin-top: 2em;
            font-size: 3vw;
            color: white;
            font-weight: bold;
            width: 97%;
            padding-bottom: .7em;
        }

        p {
            font-size: 1.6vw;
            color: white;
            font-weight: 500;
            width: 97%;
        }

        a {
            color: white;
            position: relative;
            top: 2em;
            font-size: 1vw;
            background-color: $colorOr;
            text-decoration: none;
            padding: 1vw 3vw;
            border-radius: 2em;
            font-weight: 700;
        }

    }

    &__img {
        width: 50%;
    }
}

@media only screen and (max-width:500px) {
    .Navbar {
        display: flex;
        justify-content: space-between;
        background-color: $colorV;
        padding: 1em 2.5em;

        .logo {
            font-size: 2vw;
        }

        .inscr {
            font-size: 2vw;

        }
    }

    .header {
        display: flex;
        justify-content: space-between;
        background-color: $colorV;
        padding: 0em 1em 2em 1em;
        flex-wrap: wrap;
        flex-direction: column-reverse;
        text-align: center;

        &__text {

            h1 {
                margin-top: 1em;
                font-size: 3vw;
                color: white;
                font-weight: bold;
                width: 97%;
                padding-bottom: .7em;
            }

            p {
                font-size: 1.6vw;
                color: white;
                font-weight: 500;
                width: 97%;
            }

            a {
                color: white;
                position: relative;
                top: 0em;
                font-size: 1vw;
                background-color: $colorOr;
                text-decoration: none;
                padding: 1vw 3vw;
                border-radius: 2em;
                font-weight: 700;
            }

        }

        &__img {
            width: 50%;
            height: 40%;
            position: relative;
            left: 22%;
        }


    }
}

.sec2 {
    padding: 1em;
    background-color: $colorOr;

    .asec2 {
        background-color: $colorV;
    }
}

@media only screen and (max-width:500px) {
    .sec2 {
        padding: 1em;
        background-color: $colorOr;
        display: flex;
        flex-direction: column;

        .asec2 {
            background-color: $colorV;
        }
    }
}

.sec3 {
    background-color: $colorR;

    .asec3 {
        background-color: $colorV;
    }

}

.footer {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    background-color: $colorV;
    padding: 2em 3em 1em 3em;

    &__info {
        padding: 0em 0em 0em 1em;

        h1 {
            color: $colorW;
        }

        h3 {
            color: $colorW;
            font-size: 1em;
        }
    }

    &__serv {
        padding-top: 2em;

        .a1 {
            color: white;
            font-size: 1vw;
            background-color: $colorOr;
            text-decoration: none;
            padding: 1vw 3vw;
            border-radius: 2em;
            font-weight: 700;
            margin-right: 3em;
        }

        .a2 {
            color: white;
            font-size: 1vw;
            background-color: $colorR;
            text-decoration: none;
            padding: 1vw 3vw;
            border-radius: 2em;
            font-weight: 700;
        }
    }

    &__social {
        img {
            width: 30%;
        }
    }
}

@media only screen and (max-width:500px) {
    .footer {

        &__serv {
            padding-top: 0em;
        }

        &__social {
            img {
                width: 15%;
            }
        }
    }
}

.container {
    margin: 2em auto;
    max-width: 71vw;
    box-shadow: 6px 7px 19px 3px rgba(149, 152, 154, 0.554);
    padding: 3em;
    border-radius: .3em;

    .check {

        input {
            border: 2px solid $colorOr;
            border-radius: 5px;
            padding: 5px;
            font-weight: 600;
            width: 15%;
        }

        label {
            font-weight: 700;
            padding: 1em;
        }
    }
}

hr {
    width: 7vw;
    border: 2px solid $colorOr;
}

.h2demnd {
    font-size: 1.3vw;
}

.activit√© {
    color: rgba(149, 152, 154, 0.781);
    display: flex;
    justify-content: center;
    align-items: center;
}

.agenda {
    text-align: center;
    padding-bottom: 3em;

    iframe {
        width: 76vw;
    }
}

.PRGRS {
    margin-left: 4em;
}

.hr2 {
    width: 18vw;
    border: 2px solid $colorOr;
}

.progress__bar {
    display: inline-block;
    width: 20%;
    height: 18px;
    border-radius: 20px;
    border: 1px solid $colorOr;
    background-color: $colorW;

    .bar {
        height: 100%;
        width: calc(1* calc(100% / 22));
        background-color: $colorR;
        border-bottom-left-radius: 20px;
        border-top-left-radius: 20px;
    }
}

.prgrsNum {
    display: inline-block;
    position: relative;
    left: 21vw;
    bottom: 1.7vw;
}

.addscience {
    color: white;
    font-size: 1vw;
    background-color: $colorOr;
    text-decoration: none;
    padding: 1vw 3vw;
    border-radius: 2em;
    font-weight: 700;
    margin-top: 1em;
    position: relative;
    top: 1vw;

    img {
        width: 1.5vw;
    }
}